(function() {
  'use strict';

  angular
    .module('forgetMeKnotApp')
    .service('fmkAlert', fmkAlert);

  fmkAlert.$inject = ['$rootScope', '$timeout'];
  function fmkAlert ($rootScope, $timeout) {

    $rootScope.showSuccess = false;
    $rootScope.successMessage = '';
    var showSuccessAlert = function (message) {
      $rootScope.showSuccess = true;
      $rootScope.successMessage = message;
      $timeout(function(){
        $rootScope.showSuccess = false;
      }, 5000);
    };

    $rootScope.showError = false;
    $rootScope.errorMessage = '';
    var showErrorAlert = function (message) {
      $rootScope.showError = true;
      $rootScope.errorMessage = message;
      $timeout(function(){
        $rootScope.showError = false;
      }, 5000);
    };

    return {
      showSuccessAlert : showSuccessAlert,
      showErrorAlert : showErrorAlert
    };
  }
})();

(function () {

  angular
    .module('forgetMeKnotApp')
    .service('authentication', authentication);

  authentication.$inject = ['$http', '$window'];
  function authentication ($http, $window) {

    var saveToken = function (token) {
      $window.localStorage['fmk-token'] = token;
    };

    var getToken = function () {
      return $window.localStorage['fmk-token'];
    };

    var isLoggedIn = function() {
      var token = getToken();
      var payload;

      if(token){
        payload = token.split('.')[1];
        payload = $window.atob(payload);
        payload = JSON.parse(payload);

        return payload.exp > Date.now() / 1000;
      } else {
        return false;
      }
    };

    var currentUser = function() {
      if(isLoggedIn()){
        var token = getToken();
        var payload = token.split('.')[1];
        payload = $window.atob(payload);
        payload = JSON.parse(payload);
        return {
          email : payload.email,
          name : payload.name
        };
      }
    };

    register = function(user) {
      return $http.post('/api/register', user).success(function(data){
        saveToken(data.token);
      });
    };

    login = function(user) {
      return $http.post('/api/login', user).success(function(data) {
        saveToken(data.token);
      });
    };

    logout = function() {
      $window.localStorage.removeItem('fmk-token');
    };

    return {
      currentUser : currentUser,
      saveToken : saveToken,
      getToken : getToken,
      isLoggedIn : isLoggedIn,
      register : register,
      login : login,
      logout : logout
    };
  }

})();

